}
.adBook-user>p{
	margin-left: .26rem;
	display: inline-block;
	border-bottom:1px solid rgba(233,233,233,.6);
	padding: .3rem 0;
	width: 82%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.adBook-user:nth-last-of-type(1)>p{
	border-bottom: 1px solid transparent;
}
.adBook-user>img{
	vertical-align: middle;		
}
.van-index-bar__sidebar {
	margin-right: .1rem;
}
.userCount{
	font-size: .38rem;
	background: rgb(237,237,237);
	text-align: center;
	padding: .34rem 0;
	color: #999;
}
</style>


